import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Universe from './components/pages/Universe';
import SupremeLeader from './components/pages/SupremeLeader';
import Ministry from './components/pages/Ministry';

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Routes>
        <Route exact path='/' element={<App />}/>
        <Route exact path='/universe' element={<Universe />}/>
        <Route exact path='/universe/supreme-leader' element={<SupremeLeader />}/>
        <Route exact path='/universe/ministry' element={<Ministry />}/>
        <Route exact path='/memes' element={<App />}/>
      </Routes>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
